<template>
    <p-data-table
        :value="testResults"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm"
        :loading="loading"
    >
        <p-column header="ID" field="btId" />
        <p-column header="Order Date" field="btDate" />
        <p-column header="E2" field="e2" />
        <p-column header="LH" field="lh" />
        <p-column header="PROG" field="prog" />
        <p-column header="FSH" field="fsh" />
        <p-column header="TSH" field="tsh" />
        <p-column header="PRL" field="prl" />
        <p-column header="BhCG" field="bhcg" />
        <p-column header="TESTO" field="testo" />
        <p-column header="DHEAS" field="dheas" />
        <p-column header="AMHP" field="amh" />
        <p-column header="" style="width: 50px">
            <template #body>
                <p-button label="Barcode" class="p-button-sm" />
            </template>
        </p-column>

        <template #footer>
            <p-button label="Order Test" class="p-mr-2" @click="add" />
        </template>
    </p-data-table>
</template>

<script>
import { useBloodTestsReportInit } from "@/composable/blood-tests";
import { inject, onMounted, ref } from "vue";

export default {
    setup() {
        const {
            testResults,
            loading,
            getBloodTestResults,
            attachDialogVisible
        } = useBloodTestsReportInit();

        onMounted(() => {
            getBloodTestResults();
        });

        return {
            testResults,
            loading,
            add: () => {
                attachDialogVisible.value = true;
            }
        };
    }
};
</script>
