<template>
    <blood-tests-report />
    <blood-tests-dialog />
    <blood-teat-attach-dialog />
</template>

<script>
import { onMounted, provide, ref } from "vue";
import { useStore } from "vuex";

import BloodTestsReport from "@/components/blood-tests/report.vue";
import BloodTestsDialog from "@/components/blood-tests/dialog.vue";
import BloodTeatAttachDialog from "@/components/blood-tests/attach-dialog.vue"

export default {
    components: { BloodTestsReport, BloodTestsDialog, BloodTeatAttachDialog },
    setup() {
        const store = useStore();
        const dialogVisible = ref(false);
        const attachDialogVisible = ref(false);
        const isCycle = ref(0);

        provide("dialogVisible", dialogVisible);
        provide("attachDialogVisible", attachDialogVisible);
        provide("isCycle", isCycle);

        onMounted(() => {
            store.dispatch("bloodTest/getBloodGroupTests");
            store.dispatch("provider/getProviders");
            store.dispatch("provider/getProviderLocations");
        });
    }
};
</script>
